@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Bangers|Electrolize);
@import url(https://fonts.googleapis.com/css?family=Varela+Round);
body {
  font-family: Roboto;
  margin: unset;
  background-color: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contenedor {
  padding-top: 20px;
  padding-left: 20px;
}

.lista-equipos {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.lista-jugadores {
  margin-top: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.app-bar {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    z-index: 100;
}

.flex {
    -webkit-flex: 1 1;
            flex: 1 1;
}

button {
    margin-left: 10px;
    margin-right: 10px;
}

.logo {
    width: 50px;
    height: 50px;
    background-image: url(/static/media/mi-liga-2.db573ac5.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.team-logo {
    max-width: 100px;
    cursor: pointer;
    -webkit-filter: drop-shadow(5px 5px 5px #222);
            filter: drop-shadow(5px 5px 5px #222);
}

.star {
    cursor: pointer;
}

.star:hover, .star_selected:hover {
    -webkit-filter: drop-shadow(1px 1px 2px #222);
            filter: drop-shadow(1px 1px 2px #222);
}

.star_selected {
    color: orange;
    cursor: pointer;
    -webkit-filter: drop-shadow(1px 1px #222);
            filter: drop-shadow(1px 1px #222);
}
.card {
    max-width: 100px;
    cursor: pointer;
    margin-left: 10px;
    margin-right: 10px;
}

.media {
    width: 100px;
    height: 100px;
}
.calendarios {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.juegos {
    display: -webkit-flex;
    display: flex;
}

.card-game {
    width: 200px;
    margin: 20px;
}

.logo-score {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.logo-score .team-logo {
    cursor: auto;
}

.score-game {
    font-family: 'Bangers', cursive !important;
    font-size: 40px !important;
    letter-spacing: 4px;
    -webkit-filter: drop-shadow(2px 2px 1px #999);
            filter: drop-shadow(2px 2px 1px #999);
}

.info-game {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-size: 12px;
    font-family: 'Electrolize' !important;
}
.error {
    font-family: 'Varela Round', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
}

.e404 {
    margin-top: 0;
    font-family: 'Bangers', cursive;
    letter-spacing: 6px;
}
